import React from 'react';

import Content from '../components/Content'
import '../App.css';
import Title from '../components/Title';

function AboutPage(props) {

	return (
		<div>
			<Title title={props.title} />

			<Content>
				<p>Hello, my name is <a href="https://www.linkedin.com/in/korinku/" target="_blank">André Silva</a>. I'm a Full Stack Developer from Portugal with experience in <b><i>Angular</i></b>, <b><i>React</i></b>, <b><i>Android</i></b>, <b><i>Java</i></b>, <b><i>MySQL</i></b>, <b><i>C</i></b>, <b><i>PHP</i></b>, <b><i>HTML5/CSS3</i></b>...</p>

				<p>Since I was young, I have been interested and curious about the IT area. I've always liked to know how things work. Today, I devote myself to this field with great pleasure and pride. Devoted, creative and ready for new challenges. </p>

				<p>My dream is to have my own business and become an entrepreneur (currently working on some ideas). </p>
				<p>I'm constantly learning new things, currently learning more about MERN stack ( <b><i>Mongo, Express, React, Node.js</i></b> ).</p>

				<p>My latest project, <b>Vintage Watches</b>, an android app developed to sell rare watches. It was coded in Java using Android Studio and Firebase. However, I will rebuild it using React in the next few months.</p>

			</Content>

		</div >
	);

}

export default AboutPage;