import React from 'react'

import { FaAngleDoubleDown } from 'react-icons/fa';
import { Link } from 'react-scroll';

function ScrollTo() {
	return (
		<>
			<h3 className="text-center display-6 font-weight-light">Recent Projects</h3>
			<Link to="portfolio" smooth={true} ><FaAngleDoubleDown className="scrollTop" style={{ height: 50 }} /></Link>
		</>
	)
}

export default ScrollTo
